.nav {
    text-align: center;
    position: absolute;
    left: 0;
    right: 0;
    top: 0.14rem;
    bottom: 0;
}
.boxs_icon {
  width: 18px;
  height: 30px;
  position: absolute;
  top: .20rem;
  left: .24rem;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  z-index: 999;
}
.boxs_icon li {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  float: left;
  width: 4px;
  height: 4px;
  background: #fff;
  /* flex: 1; */
  margin: 1px;
}
.boxs_icon li:nth-child(1) {
  -webkit-transform: translate(-3px, -3px);
          transform: translate(-3px, -3px);
}
.boxs_icon li:nth-child(2) {
  -webkit-transform: translate(0, -3px);
          transform: translate(0, -3px);
}
.boxs_icon li:nth-child(3) {
  -webkit-transform: translate(3px, -3px);
          transform: translate(3px, -3px);
}
.boxs_icon li:nth-child(4) {
  -webkit-transform: translate(-3px, 0);
          transform: translate(-3px, 0);
}
/* .boxs_icon li:nth-child(5) {
           transform: translate(-3px,-3px)
         } */
.boxs_icon li:nth-child(6) {
  -webkit-transform: translate(3px, 0);
          transform: translate(3px, 0);
}
.boxs_icon li:nth-child(7) {
  -webkit-transform: translate(-3px, 3px);
          transform: translate(-3px, 3px);
}
.boxs_icon li:nth-child(8) {
  -webkit-transform: translate(0, 3px);
          transform: translate(0, 3px);
}
.boxs_icon li:nth-child(9) {
  -webkit-transform: translate(3px, 3px);
          transform: translate(3px, 3px);
}
.cur_active {
  width: 18px;
}
.cur_active li {
  background: #715699;
  -webkit-transform: translate(0, 0) !important;
          transform: translate(0, 0) !important;
}
.popsi-enter-active,
.popsi-leave-active {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.popsi-enter,
.popsi-leave-active {
  /* display: none; */
  opacity: 0;
}
.content {
  position: relative;
  height: 100%;
}
@media screen and (max-width: 375px) {
.nav {
    display: none;
}
.boxs_icon {
    top: 20px;
}
}
@media screen and (min-width: 375px) and (max-width: 450px) {
